.input {
  width: 50%;
  margin-left: 50px;
  margin-top: 20px;
  outline: none;
  background: rgba(0, 0, 0, 0.25);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  backdrop-filter: blur(13px);
  -webkit-backdrop-filter: blur(13px);
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.18);
  color: white;
  padding: 20px;
}

.content-container {
  max-width: 1000px;
  margin: 0 auto;
  display: flex;
  padding: 20px;
}
